.ui-message {
	border-radius: 3px;
	text-align: left;
	padding-right: 48px;
}
.ui-message__icon {
	margin-right: 8px;
}
.ui-message__close {
	position: relative;
	width: 16px;
	height: 16px;
	cursor: pointer;
	padding: 24px;
	position: absolute;
	top: 50%;
	right: 0;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
}
.ui-message__close:after,
.ui-message__close:before {
	position: absolute;
	left: 8px;
	top: 0;
	content: '';
	height: 16px;
	width: 2px;
	background-color: #fff;
	cursor: pointer;
}
.ui-message__close:before {
	-webkit-transform: rotate(45deg) translateX(20px);
	transform: rotate(45deg) translateX(20px);
}
.ui-message__close:after {
	-webkit-transform: rotate(-45deg) translateY(20px);
	transform: rotate(-45deg) translateY(20px);
}
.ui-message--info {
